      display: block;
      margin-top: 200px;
}
.mission, .products, .founder {
  width: 85%;
  position: relative;
  display: flex;

  align-self: center;
  display: block;
  clear: both;
  margin: 0 auto;
}
.info--container {
  display: flex;
  /* flex-direction: column; */
