  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  background: #e3e3e3;
}
.react-multi-carousel-list {
  background: #e3e3e3;
  padding: 30px 10% 15px 10% !important;
  /* margin: 0 auto !important; */
}
.about {
  width: 80%;
  position: relative;
  display: flex !important;
  align-self: center;
  display: block;
  margin: 15px auto;
  align-items: center;
  background: #fff;
}
