.home--container video.videoTag {
    position: fixed;
    bottom: 0;
    right: 0;
    min-width: 100%;
    min-height: 100%;
    z-index: -1;
}
.about:before {
  content: '';
  background: yellow;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  z-index: -1;
}
.mission:before {
  content: '';
  background:#000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  z-index: -1;
}
.App .row {
  --bs-gutter-x: 0 !important;
}
.products:before {
  content: '';
  background:#fff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  z-index: -1;
}
.founder:before {
  content: '';
  background-color: #f9f2f3;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  z-index: -1;
}
.home {
  background-color: transparent !important;
  background: transparent !important;
}
.about p, .mission p, .products p, .founder p, .home p {
  font-size: 20px;
}
.founder img  {
  max-width: 100%;
}
.mission {
  margin-top: 150px;
  color: #fff;
}

.products {
  color: #000;
}
.video-container.mission-container {
  justify-content: flex-start;
}
.homeImage {
  display: flex;
  max-width: 80%;
  justify-content: space-between;
  align-items: flex-start;
  margin: 0 auto;
  padding-bottom: 80px;
}
.homeImage div {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  max-width: 33%;
}
span.caption {
  background: #fff;
  font-size: 20px;
  padding: 10px;
  margin-top: 10px;
}
.homeImage.caption {
  /* margin: 30px auto; */
  padding: 20px 0px;
  justify-content: space-between;
  text-align: center;
}

.homeImage.caption span {
  background: #fff;
  padding: 10px;
  /* font-size: 15px; */
  /* flex: 1 1; */
  flex: 1 1;
  max-width: 33%;
  font-weight: bold;  
    font-size: 20px;

}
.video-container:not(.video-home) h1 {
  z-index: 1;
  position: absolute;
  color: #fff;
  text-align: center;
  top: 50%;
  left: 0;
  right: 0;
  font-size: 50px;
  text-shadow: 2px 2px #000;
  padding: 20px;
}
.carousel-container div {
  border-radius: 10px;
}

.carousel-container div img {
  border-radius: 10px;
}

.carousel-container {
  padding-top: 15px;
}

.video-container:not(.video-home) h1:before {
content: '';
background: lightblue;
left: 0;
top: 0;
width: 100%;
height: 100%;
position: absolute;
opacity: 0.7;
z-index: -1;
}

.video-container:not(.video-home) {
  width: 100%;
  max-height: initial;
  /* overflow: hidden; */
  position: relative;
  /* top: 0; */
  /* right: 0; */
  z-index: -100;
  height: 500px;
}
.about.col-md-6.offset-md-3 p {
  font-size: 20px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.container {
  background-image: url('./assets/background.png');
    background-size: cover;
    background-repeat: no-repeat;
}
.font-video {
  width: 100%;
  height: 100%;
}
