@media(max-width: 768px) {
.about, .mission, .products, .founder, .home {
      width: auto;
      flex-direction: column-reverse;
  }
  .about .text {
    padding: 20px 20px;
}
  h1 {
    font-size: 35px !important;
  }
  p {
    font-size: 18px !important;
  }
  .footer {
    font-size: 14px;
  }
  .homeImage img {
    /* max-width: 80%; */
    margin-top: 15px;
}
.homeImage div {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  max-width: 80%;
}
.homeImage {
  flex-direction: column;
  align-items: center;
}
.homeImage.caption {
  flex-direction: column;
  align-items: center;
}

.homeImage.caption span {
  max-width: 80%;
  margin-top: 15px;

}
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
